import Model from './Model';
import genders from '../settings/genders';
import { option } from '../utils/types';

export type genderPayload = {
	text: string,
}

class Gender extends Model {

	public static get entries() {
		return genders;
	}

	public static getAvailableOptions(): option[] {
		return Object.entries(this.entries)
			.map(([key, gender]) => ({ label: gender.text, value: key }));
	}

	protected _text: string;

	constructor(payload: genderPayload) {
		super();
		this._text = payload.text;
	}

	public get text(): string {
		return this._text;
	}

}

export default Gender;
