import Model from './Model';
import Gender from './Gender';
import tools from '../settings/tools';
import { option } from '../utils/types';

export type toolPayload = {
	text: string,
	genders: Gender[],
}

class Tool extends Model {

	public static get entries() {
		return tools;
	};

	public static filterAvailable(tools: Tool[], gender: Gender): Tool[] {
		return tools
			.filter(tool => tool.genders.includes(gender))
	}

	public static getAvailableOptions(gender: Gender): option[] {
		return Object.entries(this.entries)
			.filter(([, tool]) => tool.genders.includes(gender))
			.map(([key, tool]) => ({ label: tool.text, value: key }));
	}

	protected _text: string;
	protected _genders: Gender[];

	constructor(payload: toolPayload) {
		super();

		this._text = payload.text;
		this._genders = payload.genders;
	}

	public get text(): string {
		return this._text;
	}

	public get genders(): Gender[] {
		return this._genders;
	}

}

export default Tool;
