import Model from './Model';
import Gender from './Gender';
import Tool from './Tool';
import { probability, buildableString, option } from '../utils/types';
import gameSettings from '../settings/game';

export type endingPayload = {
	text: string,
	actionText: buildableString,
	tools?: Tool[],
	genders: Gender[],
	chance: probability,
	singleAction?: boolean,
	canPostOrgasm?: boolean,
}

class Ending extends Model {

	public static get entries() {
		return gameSettings.endings;
	}

	public static filterAvailable(endings: Ending[], gender: Gender, tools: Tool[]): Ending[] {
		return endings
			.filter(ending => ending.genders.includes(gender))
			.filter(ending => {
				if (!ending.tools) {
					return true;
				}

				return ending.tools.some(tool => tools.includes(tool));
			});
	}

	public static getAvailableOptions(gender: Gender, tools: Tool[]): option[] {
		return Object.entries(this.entries)
			.filter(([, ending]) => ending.genders.includes(gender))
			.filter(([, ending]) => {
				if (!ending.tools) {
					return true;
				}

				return ending.tools.some(tool => tools.includes(tool));
			})
			.map(([key, action]) => ({ label: action.text, value: key }));
	}

	protected _text: string;
	protected _actionText: buildableString;
	protected _tools?: Tool[];
	protected _genders: Gender[];
	protected _chance: probability;
	protected _canPostOrgasm?: boolean;

	constructor(payload: endingPayload) {
		super();
		this._text = payload.text;
		this._actionText = payload.actionText;
		this._tools = payload.tools;
		this._genders = payload.genders;
		this._chance = payload.chance;
		this._canPostOrgasm = payload.canPostOrgasm;
	}

	public get text(): string {
		return this._text;
	}

	public get actionText(): buildableString {
		return this._actionText;
	}

	public get tools(): Tool[] | undefined {
		return this._tools;
	}

	public get genders(): Gender[] {
		return this._genders;
	}

	public get chance(): probability {
		return this._chance;
	}

	public get canPostOrgasm(): boolean {
		return this._canPostOrgasm ?? true;
	}

}

export default Ending;
