export type entries = { [key: string]: Model };

class Model {

	public static get entries(): entries {
		return {};
	};

	public static get keys(): string[] {
		return Object.keys(this.entries);
	}

	public static get values(): any[] {
		return Object.values(this.entries);
	}

	public static get<T = any>(key: string | string[]): T | T[] {
		if (Array.isArray(key)) {
			return key
				.map(k => this.entries[k])
				.filter(value => !!value) as any as T[];
		}

		return this.entries[key] as any as T;
	}

	public get entries(): entries {
		return (this.constructor as typeof Model).entries;
	}

	public get key(): string | null {
		const entry = Object.entries(this.entries).find(([, value]) => this === value);

		if (!entry) {
			return null
		}

		return entry[0];
	}

}

export default Model;
